<template>
  <Header>
    <Menu mode="horizontal" theme="dark" active-name="1">
      <div class="layout-logo">
        <img src="../assets/logo.png" alt="logo">
      </div>
      <div class="layout-nav">
        <MenuItem name="1">
          <router-link to="/">首页</router-link>
        </MenuItem>
        <MenuItem name="2" v-if="!user.userName">
          <router-link to="/login">登录</router-link>
        </MenuItem>
        <MenuItem name="3" v-if="!user.userName">
          <router-link to="/reg">注册</router-link>
        </MenuItem>
        <MenuItem name="4" v-if="user.userName">
          <span class="messagr">欢迎：{{user.userName}} 登录</span>
        </MenuItem>
        <MenuItem name="5" v-if="user.userName">
          <span @click.prevent="submit">退出登录</span>
        </MenuItem>
      </div>
    </Menu>
  </Header>
</template>

<script>
import { mapMutations, mapGetters } from "vuex";
export default {
  computed: {
    ...mapGetters(["user"])
  },
  methods: {
    ...mapMutations(["updateUser"]),
    submit() {
      this.updateUser({});
      this.$router.push("/login");
    }
  }
};
</script>

<style scoped>
@import url(../assets/css/header.css);
</style>


